    components:{
        SvgIcon,
        CustomerInput
    }
})
export default class AddCommon extends Vue{
    @Prop({default:()=>{}})oper_info!:any;
    @PropSync('visible')visible_sync!:boolean;
    private mirror_type_list:any=[];
    private bit_list:Array<any>=[{id:64,name:'64'},{id:32,name:'32'}];
    private az_list:Array<any>=[];
    private storage_type_list:Array<any>=[];
        // 
    private product_source_type_list:any=[];

    private compute_type_list:any=[
      {value:'GPU',label:'GPU'},
      {value:'CPU/GPU',label:''}
      ]
    private model_type_list:any=[]
    private drive_type_list:any=['Datacenter','Geforce','Enflame','GRID'];
    private file_type_list:any=['qcow2','iso'];
    private query_url:string="";
    private os_file:any=[];
    private customer_list:Array<String>=this.oper_info.os_id ? this.oper_info.white_customer_list : []
    private form_data:any={
        id:this.oper_info.os_id,
        display_name:this.oper_info.display_name ? this.oper_info.display_name : '',
        os_type:this.oper_info.os_type ? this.oper_info.os_type : '',
        os_version:this.oper_info.os_version ? this.oper_info.os_version : '',
        os_bit:this.oper_info.os_bit ? this.oper_info.os_bit : this.bit_list[0].id,
        customer_ids:'',
        size:this.oper_info.os_id ? this.oper_info.size : 20,
        az_id:this.oper_info.az_list ? this.oper_info.az_list[0] : '',
        backend_type:this.oper_info.backend_type ? this.oper_info.backend_type : '',
        support_type:(this.oper_info.support_type &&  this.oper_info.support_type !== 'GPU') ? this.compute_type_list[1].value : this.compute_type_list[0].value,//
        // 
        product_source:'',
        support_gpu_driver:this.oper_info.support_gpu_driver ? this.oper_info.support_gpu_driver : this.drive_type_list[0],
        os_file_type:this.oper_info.os_file_type ? this.oper_info.os_file_type : this.file_type_list[0],
        path_md5:this.oper_info.path_md5 ? this.oper_info.path_md5 : '',
        upload_time:this.oper_info.os_id ? this.oper_info.upload_time : new Date(),
        oss_file_name:'',
        core_version:this.oper_info.kernel_version ? this.oper_info.kernel_version : '',
        driver_version:this.oper_info.driver_version ? this.oper_info.driver_version :'',
        cuda_version:this.oper_info.cuda_version ? this.oper_info.cuda_version : '',
        other_software:this.oper_info.other_software ? this.oper_info.other_software : '',
        validity:this.oper_info.os_id ? this.oper_info.maintenance_expiration_date !== '' ? '0':'1' : '1',
        vali_time: (this.oper_info.maintenance_expiration_date !== '' && !isNaN(new Date(this.oper_info.maintenance_expiration_date).getTime())) ? this.oper_info.maintenance_expiration_date : '',
        display_name_en:this.oper_info.display_name_en ? this.oper_info.display_name_en : '',
        model_image_type:this.oper_info.model_image_type ? this.oper_info.model_image_type : '',
    }
    private rules={
        display_name: [{ required: true, validator:this.validate_name, trigger: 'change' }],
        os_type: [{ required: true, message: '', trigger: 'change' }],
        os_version: [{ required: true, message: '', trigger: 'change' }],
        os_bit: [{ required: true, message: '', trigger: 'change' }],
        az_id: [{ required: true, message: '', trigger: 'change' }],
        backend_type: [{ required: true, message: '', trigger: 'change' }],
        support_type: [{ required: true, message: '', trigger: 'change' }],
        product_source: [{ required: true, message: '', trigger: 'change' }],
        os_file_type: [{ required: true, message: '', trigger: 'change' }],
        path_md5:[{ required: true, validator:this.path_md5_check, trigger: 'change' }],
        oss_file_name:[{ required: true, message: '', trigger: 'change' }],
        driver_version:[{ required: true, message: '', trigger: 'change' }],
        cuda_version:[{ required: true, message: 'CUDA', trigger: 'change' }],
        validity:[{ required: true, validator:this.validateTime, trigger: 'change' }],
        other_software:[{ required: true, message: '', trigger: 'change' }],
        core_version:[{ required: true, message: '', trigger: 'change' }],
        display_name_en:[{ required: true, validator:this.validate_name_en, trigger: 'change' }],
        model_image_type:[{ required: true, message: '', trigger: 'change' }]
    }
    created(){
        // if(this.oper_info.os_id){
        //     this.rules.path_md5=[{required:false, message: 'MD5', trigger: 'change' }]
        // }else{
        //     this.get_mirror_type();
        //     this.get_disk_list()
        // }
      this.get_mirror_type();
      this.get_disk_list()
      this.get_product_source()
      this.$route.name === 'model_mirror_list' && this.get_model_image_list()
    }
    private path_md5_check(rule, value, callback){
        if(!value) {
            return callback(new Error('MD5'))
        } else if(value.length < 32){
            return callback(new Error('32MD5'))
        } else {
            return callback()
        }
    }
    private validateTime(rule, value, callback) {
      if(value === '0'){
        if(!this.form_data.vali_time){
          return callback(new Error(''))
        }else {
          return callback()
        }
      }else{
        callback()
      }
    }
    private validate_name(rule, value, callback){
        if(!value){
            return callback(new Error(''))
        }else if(this.form_data.display_name.length> 128) {
            return callback(new Error('128'))
        } else {
            Service.check_name({
                os_id:this.oper_info.os_id ? this.oper_info.os_id : '',
                display_name:value,
            }).then(res=>{
                if(res.code==='Success'){
                    if(res.data.usable){
                        return callback()
                    }else{
                        return callback(',')
                    }
                }else{
                    return callback('128')
                }
            }).catch(err=>{
                return callback('128')
            })

        }
    }
    private validate_name_en(rule, value, callback){
    if(!value){
      return callback(new Error(''))
    }else if(this.form_data.display_name_en.length> 128) {
      return callback(new Error('128'))
    } else {
      Service.check_name({
        os_id:this.oper_info.os_id ? this.oper_info.os_id : '',
        display_name_en:value,
      }).then(res=>{
        if(res.code==='Success'){
          if(res.data.usable_en){
            return callback()
          }else{
            return callback(',')
          }
        }else{
          return callback('128')
        }
      }).catch(err=>{
        return callback('128')
      })

    }
  }
    private FnDisable(date){
        return date>new Date()
    }
    //
    private async get_product_source(){
      let res:any = await Service.get_product_source()
      if(res.code==="Success"){
        this.product_source_type_list = res.data
        this.form_data.product_source = this.oper_info.product_source ? this.oper_info.product_source:this.product_source_type_list[0].value
      } else {
        this.$message.error(res.message)
      }
    }
    private async get_disk_list(){
        let res:any = await Service.get_disk_list({})
        if(res.code==="Success"){
            for(let i in res.data){
                this.storage_type_list.push({id:i,name:res.data[i]})
                if(this.oper_info.os_id && this.form_data.backend_type===res.data[i]){
                    this.form_data.backend_type=i
                }
            }
            if(!this.oper_info.os_id){
                this.form_data.backend_type = this.storage_type_list[0].id
            }

        }
    }
    @Watch('form_data.backend_type')
    private watch_backend_type(){
        if(!this.oper_info.os_id){
            this.form_data.az_id=''
            this.get_available_az()
        }

    }
    private async get_available_az(){
        let res:any = await Service.get_available_az({
            backend_type:this.form_data.backend_type,
            employee_no:this.$store.state.employee_no,
            user_name:this.$store.state.login_name
        })
        if(res.code==="Success"){
            this.az_list=res.data;
            this.form_data.az_id = this.az_list[0].az_id
        }
    }
    private async get_mirror_type(){
        let res:any = await Service.get_mirror_type({})
        if(res.code==="Success"){
            this.mirror_type_list=res.data.type_list;
            if(!this.oper_info.os_id){
              this.form_data.os_type = this.mirror_type_list[0]
            }
        }
    }
    private async get_model_image_list(){
      let res:any = await Service.get_model_image_type({})
      if(res.code==="Success"){
        this.model_type_list=res.data.type_list;
        if(!this.oper_info.os_id){
          this.form_data.model_image_type = this.model_type_list[0]
        }
      }
    }
//     private async get_az_list(){
//     let res:any=await EcsService.get_region_az_list({})
//     if(res.code==="Success"){
//       res.data.forEach(item=>{
