//     }
//   }
    private before_upload(file){
    }
    private FnSuccess(response, file, fileList){
    }
    @Watch('form_data.os_type')
    private watch_os_type(nv){
        if(nv==='Windows'){
            this.form_data.size = Math.max(this.form_data.size,40)
        }else{
            this.form_data.size = Math.max(this.form_data.size,20)
        }
    }
    @Watch('form_data.os_file_type')
    private watch_os_file_type(nv){
        if(nv==='iso'){
            this.form_data.support_type=''
        }
        // this.query_url = str==="" ? "" : `?${str.slice(0,str.length-1)}`;
        //  console.log("this.query_url",this.query_url)

    }
    // @Watch("form_data",{immediate:true,deep:true})
    // private watch_form_data(){
    //     const {display_name,os_type,os_version,os_bit,customer_ids,az_id,backend_type,support_type,support_gpu_driver,os_file_type,path_md5}=this.form_data;
    //     let obj={
    //         display_name,
    //         os_type,
    //         os_version,
    //         os_bit,
    //         customer_ids,
    //         az_id,
    //         backend_type,
    //         support_type,
    //         support_gpu_driver,
    //         os_file_type,
    //         path_md5,
    //     }
    //     let str=""
    //     for (let i in obj){
    //         if(obj[i]){
    //             str =str+`${i}=${obj[i]}&`
    //         }
    //     }
    //     this.query_url = str==="" ? "" : `?${str.slice(0,str.length-1)}`;
    // }
    private FnCustomer(val){
        this.form_data.customer_ids =val? val.join(',') : ''
    }
    private async confirm(){
        const form= this.$refs.mirror_form as Form;
        const {product_source,display_name,os_type,os_version,os_bit,size,customer_ids,az_id,backend_type,support_type,support_gpu_driver,oss_file_name,os_file_type,path_md5,upload_time,core_version,driver_version,cuda_version,other_software,validity,vali_time,display_name_en,model_image_type}=this.form_data
        form.validate(async valid=>{
            if(valid){
                if(this.oper_info.os_id){
                    let res:any = await Service.edit_pub_mirror({
                        os_id:this.oper_info.os_id,
                        display_name,
                        customer_ids:customer_ids==='' ? [] : customer_ids.split(','),
                        backend_type,
                        support_type,
                        product_source,
                        support_gpu_driver:support_type==='GPU' ? support_gpu_driver : undefined,
                        os_file_type,
                        path_md5,
                        size,
                        kernel_version:core_version,
                        driver_version: support_type==='GPU' ? driver_version : undefined,
                        cuda_version: support_type==='GPU' ? cuda_version : undefined,
                        other_software: support_type==='GPU' ? other_software : undefined,
                        maintenance_expiration_date:validity === '1' ? 'LongTerm' : vali_time,
                        display_name_en,
                        os_type,
                        os_version,
                        os_bit,
                        model_image_type: this.$route.name === 'model_mirror_list' ? model_image_type : undefined,
                    })
                    if(res.code==='Success'){
                        this.$message.success(res.message)
                    }
                }else{
                    // const upload = this.$refs.upload as any;
                    // upload.submit()
                    let res:any = await Service.add_pub_mirror({
                        display_name,
                        os_type,
                        os_version,
                        os_bit,
                        customer_ids,
                        az_id,
                        size,
                        backend_type,
                        support_type,
                        product_source,
                        support_gpu_driver:support_type==='GPU' ? support_gpu_driver : undefined,
                        os_file_type,
                        path_md5,
                        oss_file_name,
                        kernel_version:core_version,
                        driver_version: support_type==='GPU' ? driver_version : undefined,
                        cuda_version: support_type==='GPU' ? cuda_version : undefined,
                        other_software: support_type==='GPU' ? other_software : undefined,
                        maintenance_expiration_date:validity === '1' ? 'LongTerm' : vali_time,
                        display_name_en,
                        model_image_type: this.$route.name === 'model_mirror_list' ? model_image_type : undefined,
                    })
                    if(res.code==='Success'){
                        this.$message.success(res.message)
                    }
                }

                this.visible_sync=false
            }
        })
    }
    private cancel(){
        const form= this.$refs.mirror_form as Form;
        form.resetFields()
        this.visible_sync=false
    }
}
